<template>
  <tbody>
    <tr>
      <td>{{ order_data.id }}</td>
      <td>{{ order_data.name }}</td>
      <td>{{ order_data.email }}</td>
      <td>{{ order_data.phone }}</td>
      <!-- <td>{{ order_data.city }}</td> -->
      <td v-if="order_data.post === 'delivery'"></td>
      <td v-if="order_data.post === 'pickup'"></td>
      <td>{{ order_data.price }} </td>
      <td>
        {{ new Date(order_data.date).toLocaleDateString() }}
      </td>
      <td>
        <select
          class="status"
          id="colors"
          v-model.trim="order_data.status"
          @change="onChangeSelectedTask"
          v-bind:class="changeBackground"
        >
          <option
            value=" "
            :selected="order_data.status === ' '"
          >
             
          </option>
          <option
            value=""
            :selected="order_data.status === ''"
          >
            
          </option>
          <option value="" :selected="order_data.status === ''">
            
          </option>
        </select>
      </td>
      <td class="figure__td">
        <button id="show-modal" class="figure__close" @click="showModal = true">
          <img
            src="../assets/img/delete.png"
            id="show-modal"
            @click="showModal = true"
            alt=""
          />
        </button>

        <router-link :to="`/clients/${order_data.id}`" class="figure__editing"
          ><img src="../assets/img/editing.png" alt=""
        /></router-link>
      </td>
      <modalClient
        v-if="showModal"
        @close="showModal = false"
        v-bind:order_modal="order_data"
        @deleteClient="deleteClient(order_data)"
      >
      </modalClient>
    </tr>
  </tbody>
</template>

<script>
import axios from "axios";
import modalClient from "@/components/modal/modal-client.vue";

import { mapActions, mapGetters } from "vuex";
import Popupclient from "./popupclient.vue";
export default {
  name: "table-clients",
  data() {
    return {
      costumersid: "",
      renderComponent: true,
      showModal: false,
      // selectedTask: "",
      styleobj: {
        backgroundColor: "green",
        fontSize: 20,
      },
    };
  },
  components: {
    modalClient,
  },
  props: {
    order_data: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  computed: {
    changeBackground: function () {
      return {
        info: this.order_data.status === " ",
        success: this.order_data.status === "",
        danger: this.order_data.status === "",
      };
    },
  },

  methods: {
    ...mapActions(["GET_COSTUMERS"]),
    onChangeSelectedTask: function () {
      axios
        .put(
          `https://admin.xfence.ru/api/admin/v1/orders/${this.order_data.id}`,
          {
            status: this.order_data.status,
          }
        )
        .then(() => {
          this.GET_COSTUMERS();
          console.log(" ");
        })
        .catch((error) => {
          console.error(error);
        });
    },

    deleteClient(order_data) {
      console.log(" del table", order_data);
      this.$emit("deleteClient");
    },
  },
};
</script>

